.dateTimePicker {
    margin-top: 150px;
    color: white;
    /* margin: 0 auto; */
    margin-bottom: 50px;
}

.dateTimePicker_header {
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 20px;
}

.dateTimePicker__arowLeft {
    height: 25px;
    width: 25px;
    transform: rotate(90deg);
    stroke: white;
}

.dateTimePicker__arowRight {
    height: 25px;
    width: 25px;
    stroke: white;
    transform: rotate(-90deg);
}

.dateTimePicker__year {
    background: var(--green);
    padding: 10px;
    border-radius: 10px;
}

.dateTimePicker_header span {
    margin: 10px;
    cursor: pointer;
}

.dateTimePicker_schedule {
    display: flex;
    overflow: hidden;
}